import { NavLink } from "react-router-dom";

const Sidebar = () => {
    return (
        <div className="w-1/4 bg-[#DBF9FB] py-6 h-[100%]">
          <div className="w-full">
            <div className="mb-4 pl-10">
                <NavLink to='/students/add'><button className="btn-primary"><span>Add Student</span></button></NavLink>
            </div>
            <div className="mb-4 mx-auto">
                <ul>
                    <NavLink to='/'><li className="text-secondary pl-10 py-4 text-[#7C878E]" >My Students (45)</li></NavLink>
                    <NavLink to='/applications'><li className="text-secondary pl-10 py-4 text-[#7C878E]">Applications</li></NavLink>
                    <li className="text-secondary pl-10 py-4 text-[#7C878E]">Active (25)</li>
                    <li className="text-secondary pl-10 py-4 text-[#7C878E]">Inactive (20)</li>
                    <li className="text-secondary pl-10 py-4 text-[#7C878E]">Deferred (15)</li>
                    <li className="text-secondary pl-10 py-4 text-[#7C878E]">Junk (45)</li>
                </ul>
            </div>
          </div>
        </div>
    )
}

export default Sidebar;