import axios from 'axios';

const api = axios.create({
    baseURL: 'http://139.59.25.83:5000',
    withCredentials: true,
    headers: {
        "Content-Type": "application/json",
        Accept: "application.json"
    }
})

export const getUsers = async () => api.get('/api/users/allusers');
export const getApplication = async () => api.get('/api/application/all');
export const updateStatus = async (data) => api.post('/api/application/updatestatus', data)
export const adminLogin = async (data) => api.post('/api/admin/login', data)
export const autoLogin = async () => api.post('/api/admin/auto');
export const addUser = async (data) => api.post('/api/admin/adduser', data);


export const getFieldOfStudy = async () => api.get('/api/fields/fiedldofstudy');
export const getCourseLevels = async () => api.get('/api/fields/courselevels');


export default api;