import { useState, useEffect } from "react";
import { getUsers, getApplication, updateStatus } from "../http";
import { toast } from 'react-toastify';
import axios from "axios";


const Applications = () => {

    const [application, setApplication] = useState([])
    const [status, setStatus] = useState({})

    useEffect(() => {
        const fetchApplication = async () => {
            const {data} = await getApplication();
            console.log(data)
            setApplication(data)
        }
        fetchApplication()
    }, [])

    const updateStatus = (e, id) => {
        setStatus({
            ...status,
            [id]: e
        })
    }

    const  handleUpdate = async (id) => {
        if(!status[id]){
            toast.error('Please change status', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
              })
            return
        }

        const dataObj = {
            status: status[id],
            id,
        }
        console.log(dataObj)
        const { data } = await axios.post('http://localhost:5000/api/application/updatestatus', dataObj)
        
        if(data.success){
            toast.success('Application status updated.', {
                position: "bottom-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
              })
        }
    }


    return (
        <div className="p-6 w-3/4 bg-[#F1F1F1]">
        <div className="board flex justify-between w-full">
           <div className="left w-[80%]">
               <div className="top flex justify-between">
                   <div className="w-[24%] p-4 bg-[#0E3454] text-center rounded-md text-[#ffffff]">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Submitted to applyassist</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#0E3454] text-center rounded-md text-[#ffffff]">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Lodged with Instituitions</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#0E3454] text-center rounded-md text-[#ffffff]">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Offers Received</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#0E3454] text-center rounded-md text-[#ffffff]">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Offers Received</p>
                   </div>
               </div>
               <div className="top flex justify-between mt-3">
                   <div className="w-[24%] p-4 bg-[#ffffff] text-center rounded-md">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Students not yet submitted</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#ffffff] text-center rounded-md">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Missing info/documents</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#ffffff] text-center rounded-md">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Missing info/documents</p>
                   </div>
                   <div className="w-[24%] p-4 bg-[#ffffff] text-center rounded-md">
                       <h1 className="text-pri">00</h1>
                       <p className="text-nl">Missing Visa info/documents</p>
                   </div>
               </div>
           </div>
           <div className="right w-[19%] bg-primary text-[#ffffff] rounded-md flex justify-center items-center">
               <div className="text-center">
               <h1 className="text-[66px]">00</h1>
               <p className="text-[23px]">Students<br/> Commenced</p>
               </div>
           </div>
        </div>

        <div className="my-6">
           <div className="flex justify-between">
               <div className="flex items-center w-[500px]">
                   <div className="text-pri font-semibold mr-6">Students</div>
                   <div className="border border-[#707070] px-4 py-1 bg-[#ffffff] rounded-3xl">
                       <input type='text' className="outline-none text-nl" placeholder="search" />
                   </div>
               </div>
               <div className="flex items-center">
               <div className="border mr-6 border-[#707070] px-4 py-1 bg-[#ffffff] rounded-3xl">
                       <input type='text' className="outline-none text-nl" placeholder="search" />
                   </div>
                   <div className="border border-[#707070] px-4 py-1 bg-[#ffffff] rounded-3xl">
                       <input type='text' className="outline-none text-nl" placeholder="search" />
                   </div>
               </div>
                  
               
           </div>
        </div>

        <dtv className="mt-6 w-full">
           <table className="w-full">
               <thead className="bg-[#FBFBFB] px-2 rounded-sm mb-1">
                   <td className="py-2 px-2">
                       <input type='radio' />
                   </td>
                   <td className="text-[#7C878E] text-nl">
                     Sl
                   </td>
                   <td className="text-[#7C878E] text-nl">Student</td>
                   <td className="text-[#7C878E] text-nl">Category</td>
                   <td className="text-[#7C878E] text-nl">Stage</td>
                   <td className="text-[#7C878E] text-nl">Status</td>
                   <td className="text-[#7C878E] text-nl">Sales/ Counsellor</td>
                   <td className="text-[#7C878E] text-nl">Next Action</td>
                   <td className="text-[#7C878E] text-nl">Notes</td>
                   <td className="text-[#7C878E] text-nl">Lead Age</td>
                   <td></td>
               </thead>
               <tbody className="mt-4">
                   

                   {
                       application.map((appli,i) => (
                           <tr className="bg-[#ffffff] my-4" key={i}>
                       <td className="px-2 py-6">
                           <input type="radio" />
                       </td>
                       <td>
                           <p className="text-nl">{i +1}</p>
                       </td>
                       <td>
                           <p className="text-nl">{appli.personal_info?.first_name} {appli.personal_info?.last_name}</p>
                           <p className="text-nl">ID: {appli.personal_info?.user_id.substring(0, 6)}...</p>
                       </td>
                       <td>
                           <p className="text-nl">Applicant</p>
                       </td>
                       <td>
                           <p className="text-nl">LOA</p>
                       </td>
                       <td>
                           <select onChange={(e) => updateStatus(e.target.value, appli._id)} className="max-w-[100px]">
                             <option value='Pending' selected={appli.status === 'Pending' ? true : false}>Pending</option>
                             <option value='Submitted' selected={appli.status === 'Submitted' ? true : false}>Submitted</option>
                             <option value='Application Review' selected={appli.status === 'Application Review' ? true : false}>Application Review</option>
                             <option value='Application Lodged with Institute' selected={appli.status === 'Application Lodged with Institute' ? true : false}>Application Lodged with Institute</option>
                             <option value='Clarification Sought by Institute' selected={appli.status === 'Clarification Sought by Institute' ? true : false}>Clarification Sought by Institute</option>
                             <option value='Application Rejected' selected={appli.status === 'Application Rejected' ? true : false}>Application Rejected</option>
                             <option value='Offer/ Conditional Offer Received' selected={appli.status === 'Offer/ Conditional Offer Received' ? true : false}>Offer/ Conditional Offer Received</option>
                             <option value='Fee Payment to Institute' selected={appli.status === 'Fee Payment to Institute' ? true : false}>Fee Payment to Institute</option>
                             <option value='COE/LOA Received' selected={appli.status === 'COE/LOA Received' ? true : false}>COE/LOA Received</option>
                             <option value='Visa Application Preparation' selected={appli.status === 'Visa Application Preparation' ? true : false}>Visa Application Preparation</option>
                             <option value='Inputs Supporting documents Needed' selected={appli.status === 'Inputs Supporting documents Needed' ? true : false}>Inputs Supporting documents Needed</option>
                             <option value='Visa Application Lodged' selected={appli.status === 'Visa Application Lodged' ? true : false}>Visa Application Lodged</option>
                             <option value='Clarification Sought' selected={appli.status === 'Clarification Sought' ? true : false}>Clarification Sought</option>
                             <option value='Visa Granted' selected={appli.status === 'Visa Granted' ? true : false}>Visa Granted</option>
                             <option value='Visa Rejected' selected={appli.status === 'Visa Rejected' ? true : false}>Visa Rejected</option>
                           </select><br/>
                           <button className="text-green-500 text-sm absolute" onClick={() => handleUpdate(appli._id)}>Update</button>
                       </td>
                       <td>
                           <p className="text-nl">Jane Doe</p>
                       </td>
                       <td>
                           <p className="text-nl">Visa Application</p>
                       </td>
                       <td></td>
                       <td>
                           <p className="text-nl">3 days</p>
                       </td>
                       <td></td>
                   </tr>
                       ))
                   }
                  
               </tbody>
           </table>
        </dtv>
   </div>
    )
}

export default Applications;