export const VerifyPersonalData = (data) => {
   if(!data.dob || data.dob === null){
      return false
   }
   if(data.city == null || data.city.length < 2){
    return false
   }
   if(data.address == null || data.address.length < 2){
    return false
   }
   if(data.country_of_birth == null || data.country_of_birth.length < 2){
    return false
   }
   if(data.dual_citizenship == null || data.dual_citizenship.length < 2){
    return false
   }
   if(data.first_name == null || data.first_name.length < 2){
    return false
   }
   if(data.last_name == null || data.last_name.length < 2){
    return false
   }
   if(data.marital_status == null || data.marital_status.length < 2){
    return false
   }
   if(data.nationality == null || data.nationality.length < 2){
    return false
   }
   if(data.postal_code == null || data.postal_code.length < 2){
    return false
   }
   if(data.state == null || data.state.length < 2){
    return false
   }
   if(data.gender == null || data.gender.length < 2){
    return false
   }
   
   return true
}

export const VerifyEducationData = (data) => {
   if(data.attended_from == null || data.attended_from.length < 2){
      return false
   }
   if(data.country_of_institution == null || data.country_of_institution.length < 2){
      return false
   }
   if(data.course_status == null || data.course_status.length < 2){
      return false
   }
   if(data.course_status === 'Completed'){
      if(data.attended_till == null || data.attended_till.length < 2){
         return false
      }
      if(data.marks === null || !data.marks){
         return false
      }
   }
   if(data.degree_name == null || data.degree_name.length < 2){
      return false
   }
   if(data.level_of_education == null || data.level_of_education.length < 2){
      return false
   }
   if(data.name_of_institution == null || data.name_of_institution.length < 2){
      return false
   }
   if(data.primary_language_of_instruction == null || data.primary_language_of_instruction.length < 2){
      return false
   }

   return true
}

export const VerifyEnglishData = (data) => {
   if(data.exam_status == null || data.exam_status.length < 2){
      return false
   }
   if(data.exam_status === "Already Gave the Exam"){
      if(data.test_name === null || data.test_name.length < 2){
         return false
      }
      if(data.listening == null || !data.listening){
         return false
      }
      if(data.over_all == null || !data.over_all){
         return false
      }
      if(data.reading == null || !data.reading){
         return false
      }
      if(data.speaking == null || !data.speaking){
         return false
      }
   }

   return true
}

export const VerifyEmp = (data) => {
   if(data.occupation === null || ! data.occupation){
      return false
   }
   if(data.satart_date === null || !data.satart_date){
      return false
   }
   if(data.end_date === null || !data.end_date){
      return false
   }
   if(data.company === null || !data.company){
      return false
   }
   if(data.type_of_employment === null || !data.type_of_employment){
      return false
   }
   if(data.field_of_experience === null || !data.field_of_experience){
      return false
   }
   return true
}

