import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {Navigate, useNavigate} from 'react-router-dom'
import { setAuth, setAdmin } from '../store/authSlice';
import { adminLogin } from "../http";



const Login = () => {

  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [errMsg, setErrMsg] = useState('');
  const dispatch = useDispatch()
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    
    if(email.length < 1){
      return setErrMsg('Email is required')
    }
    if(password.length < 1){
      return setErrMsg('Password is required')
    }

    try {
      const {data} = await adminLogin({email, password})
      
      dispatch(setAdmin({admin: data.admin}))
      dispatch(setAuth({isAuth:true}))
      navigate('/')
      
      
    } catch (error) {
      if(error){
        setErrMsg(error.response.data.message)
      }
    }
  }

  const {isAuth} = useSelector((state) => state.auth);



  if(isAuth){
    return <Navigate to='/' />
  }

    return (
        <div className="login">
        <div className="py-3 w-full flex justify-center items-center shadow-md">
          <img src='./assets/images/logo.png' alt="logo" className="w-[220px]" />
        </div>
  
  
  
  
        <div className="main logsc f-full h-[80vh]">
          <div className="mx-auto flex max-w-screen-lg h-[100%] items-center">
            <div className="w-full relative h-[100%] items-end pb-[7%] hidden md:flex">
              <div className="right-content w-[450px]">
                <h1 className="text-[#ffffff] text-4xl font-bold">Welcome!</h1>
                <p className="text-[#ffffff] text-sm mt-5">You will need a valid Google/Facebook account to sign in
                  to applyassist. If you have installed third-party applications
                  or browser extensions to block pop-up windows, be sure to allow
                  the sign-in window for the social media account you will use to
                  sign in to applyassist.</p>
                <p className="text-[#ffffff] text-sm mt-3"> By proceeding to login, you agree that you have read and accepted
                  our terms, you're at least 13 years old, and you consent to our privacy
                  policy and receiving marketing communications from us.</p>
              </div>
            </div>
            <div className="flex w-full justify-center items-center">
            <div className="form w-[400px] bg-[#ffffff] p-[30px] rounded-3xl">
               
               
               <form className="mt-5">
                <div className="input-box">
                  <label className="form-label">Email</label>
                  <input value={email} onChange={(e) => setEmail(e.target.value)} type="email" id="mobile_number"  className="input-fields w-full border outline-none h-[40px] rounded-3xl pl-[30px] mt-4" />
                </div>
                <div className="input-box">
                  <label className="form-label">Password</label>
                  <input value={password} onChange={(e) => setPassword(e.target.value)} type="password" id="mobile_number"  className="input-fields w-full border outline-none h-[40px] rounded-3xl pl-[30px] mt-4" />
                </div>
                <div className="py-2">
                  <p className="text-xs text-red-500">{errMsg}</p>
                </div>
                <div className="input-box text-center">
                  <input onClick={(e) => handleLogin(e)} type="submit" value="Login" className="w-[150px] mx-auto mt-4 py-2 rounded-3xl border-0 bg-primary text-white" />
                </div>
                <div className="social-login mt-2 flex  space-x-3 justify-between w-[90%] mx-auto">
         
        </div>
        <div className="flex justify-center mt-2">
         <span className="text-xs text-center text-[#7C878E] leading-5">
         By continuing, you agree to applyassist's Terms of Service and acknowledge you've read our Privacy Policy. Notice at collection.
         </span>
        </div>
        </form>

     </div>
            </div>
          </div>
        </div>
  
        <div className="login-footer pt-5 w-full bg-secodary h-[20vh]">
          <div className="top fles justify-between flex flex-col md:flex-row items-center md:justify-between max-w-screen-xl mx-auto">
           <img src='/assets/images/logofooter.png' alt="logo" className="w-[200px]" />
           <ul className="flex">
             <li className="nav-link text-[#ffffff] mx-4">Terms</li>
             <li className="nav-link text-[#ffffff] mx-4">Privacy</li>
             <li className="nav-link text-[#ffffff] mx-4">FAQ</li>
             <li className="nav-link text-[#ffffff] mx-4">Contact</li>
           </ul>
          </div>
          <div className="copyright flex justify-center px-4 text-center mt-8">
           <p className="text-[#AEBAC2] text-sm">Copyright© 2022 Touchstone Education Asia Pacific Pty Ltd. All Rights Reserved.</p>
          </div>
         </div>
      </div>
    )
}
export default Login;